  /*************************************************+++++FOTOS */
  async takePicture(op) {
  const image = await Plugins.Camera.getPhoto({
  correctOrientation: true,
            height: 200,
            width: 200,
                quality: 60,
  allowEditing: true,
            resultType: CameraResultType.DataUrl,
            source: CameraSource.Camera
          });
          this.authenticationService.generateFromImage(image.dataUrl, 400, 400, 1, data => {
            switch (op) {
              case 'foto0a': this.foto0img = data;   this.Form.controls['foto0'].setValue( this.authenticationService.procesafoto(this.Form.controls['foto0'].value)); break;
              case 'logo1a': this.logo1img = data;   this.Form.controls['logo1'].setValue( this.authenticationService.procesafoto(this.Form.controls['logo1'].value)); break;
                default:
            }
        });
        }
  uploadPWA2(op , controlx) {
         const fileList: FileList = controlx;
         if (fileList && fileList.length > 0) {
                 this.firstFileToBase64(fileList[0]).then((result: string) => {
                 this.authenticationService.generateFromImage(result, 400, 400, 1, data => {
                 switch (op) {
                         case 'foto0a':  this.foto0img = data;    this.Form.controls['foto0'].setValue( this.authenticationService.procesafoto(this.Form.controls['foto0'].value)); break;
                         case 'logo1a':  this.logo1img = data;    this.Form.controls['logo1'].setValue( this.authenticationService.procesafoto(this.Form.controls['logo1'].value)); break;
                          default:
                  }
                 });
                 }, (err: any) => {
                 switch (op) {
                 case 'foto0a':  this.foto0img = null;  this.Form.controls['foto0'].setValue('');  break;
                 case 'logo1a':  this.logo1img = null;  this.Form.controls['logo1'].setValue('');  break;
                 default:
                 }
                 });
  }}
  uploadPWA(op) {
  switch (op) {
  case 'foto0a':  if (this.foto0a == null ) { return;}
  this.uploadPWA2(op , this.foto0a.nativeElement.files); break;
  case 'logo1a':  if (this.logo1a == null ) { return;}
  this.uploadPWA2(op , this.logo1a.nativeElement.files); break;
  default:
  }
  }
  private firstFileToBase64(fileImage: File): Promise<{}> {
  return new Promise((resolve, reject) => {
  const fileReader: FileReader = new FileReader();
  if (fileReader && fileImage != null) {
  fileReader.readAsDataURL(fileImage);
  fileReader.onload = () => {
  resolve(fileReader.result);
  };
  fileReader.onerror = (error) => {
  reject(error);
  };
  } else {
  reject(new Error('No file found'));
  }
  });
  }
